<template>
  <div  style="min-width: 1200px">
    <div class="mlist-control ant-list-bordered">
      <div class="list-control-finish">
        <a-radio-group  :value="needsType" @change="handleTypeChange">
          <a-radio-button value="全部" name="全部">全部</a-radio-button>
          <a-radio-button value="匹配中" name="匹配中">匹配中</a-radio-button>
          <a-radio-button value="匹配完成待支付" name="匹配完成待支付">匹配完成待支付</a-radio-button>
          <a-radio-button value="交易成功" name="交易成功">交易成功</a-radio-button>
          <a-radio-button value="匹配失败" name="匹配失败">匹配失败</a-radio-button>
        </a-radio-group>
      </div>
      <div style="margin-left: auto;">
        <router-link to="/demand" class="list-control-new">
          <a-button type="primary" ghost style="width:150px">发布需求</a-button>
        </router-link>
      </div>
    </div>

    <a-list
      class="listpage"
      :bordered="true"
      :pagination="paginationProps"
      :loading="loading"
      itemLayout="horizontal"
      :dataSource="data"
      :style="{backgroundImage: 'url(' + img + ')',
          backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100%'
          }"
      style="min-width: 1200px"
    >
      <a-list-item slot="renderItem" slot-scope="item">
        <a-button type="primary" slot="actions" @click="getDemandDetail(item.id)">查看</a-button>

        <a-list-item-meta :description="item.needsDes">
          <a slot="title">{{item.needsFarmerType}}</a>
          <a-avatar slot="avatar" shape="square" style="width:50px;height:50px;padding-top: 25px;" :style="machineStatusStyleAvatar(item.needsType)" >Task</a-avatar>
        </a-list-item-meta>

        <span class="ant-list-item-meta-description" style="width:150px;margin-right:30px">
          发布时间
          <br />
          {{ item.needsBeginTime }}
        </span>
        <span class="ant-list-item-meta-description" style="width:150px;margin-right:30px">
          开始时间
          <br />
          {{ item.needsTime }}
        </span>
        <span
          :style="machineStatusStyle(item.needsType)"
          style="display:inline-block;width:200px;align-self:center"
        >{{ item.needsType }}</span>
        <!-- <a-progress
          :percent="item.progress"
          status="active"
          style="display:inline-block;width:200px;align-self:center;"
        />-->
      </a-list-item>

      <br />
    </a-list>

    <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
  </div>
</template>
<script>
import { getDemandList } from "../../api/login"
//import reqwest from 'reqwest'

export default {
  data() {
    return {
      id: localStorage.getItem("userId"),
      loading: false,
      loadingMore: false,
      showLoadingMore: true,
      needsType: "全部",
      needList: [],
      filterList: [],
      img:require('../../assets/dddb-webEnt/outbg.jpg')
    };
  },
  computed: {
    list() {
      // return this.$store.state.taskList;
    },
    paginationProps() {
      let pag = {
        pageSize: 6,
        total: this.data.length
      };
      return pag;
    },
    data() {
      let list = [...this.needList];
      if (this.needsType === "匹配中") {
        list = list.filter(item => item.needsType == "匹配中");
      } else if (this.needsType === "匹配完成待支付") {
        list = list.filter(item => item.needsType == "匹配完成待支付");
      } else if (this.needsType === "交易成功") {
        list = list.filter(item => item.needsType == "交易成功");
      } else if (this.needsType === "匹配失败") {
        list = list.filter(item => item.needsType == "匹配失败");
      }
      return list;
    }
  },
  mounted() {
    this.$parent.showMenu=true
    // this.$store.dispatch("gettaskList");
    this.getDemandsList();
    // this.getData((res) => {
    //   this.loading = false
    //   this.data = res.results
    // })
  },
  methods: {
    // 动态样式
    machineStatusStyleAvatar(e){
      if(e == '匹配中'){
        return {
          'background-color':'#D39846'
        }
      }else if(e == '匹配失败'){
        return {
         'background-color':'#ABABAA'
        }
      }else if(e == '匹配完成待支付'){
        return {
          'background-color':'#314659'
        }
      }else if(e == '交易成功'){
        return {
          'background-color':'#4F5F46'
        }
      };
    },
    machineStatusStyle(e){
      if(e == '匹配中'){
        return {
          'color':'#D39846'
        }
      }else if(e == '匹配失败'){
        return {
          'color':'#ABABAA'
        }
      }else if(e == '匹配完成待支付'){
        return {
          'color':'#314659'
        }
      }else if(e == '交易成功'){
        return {
          'color':'#4F5F46'
        }
      };
    },
    getDemandsList() {
      getDemandList(this.id)
        .then(response => {
          this.needList = response.data.data.needList;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // getData  (callback) {
    //   reqwest({
    //     url: fakeDataUrl,
    //     type: 'json',
    //     method: 'get',
    //     contentType: 'application/json',
    //     success: (res) => {
    //       callback(res)
    //     },
    //   })
    // }
    handleTypeChange(e) {
      this.needsType = e.target.value;
    },
    onSearch() {},
    getDemandDetail(itemId) {
      this.$router.push({
        name: "demanddetail",
        query: {
          id: itemId
        }
      });
    }
  }
};
</script>
<style scoped>
.list-control {
  background: #fff;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 16px;
  height: 60px;
  display: flex;
  flex-direction: row;
}
/* .list-control-info {
  display: inline-block;
  align-self: center;
  margin-right: 10px;
} */
.list-control-finish {
  margin-right: 50px;
}
.list-control-new {
  width: 150px;
  margin-left: 450px;
}

.listpage {
  min-height: 500px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 40px 0px 40px;
  width: 90%;
}
.Content {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.ant-list-item-meta-title :global(a){
  color: rgba(0, 0, 0, 0.65);
  font-weight: bold;
}
</style>



